* {
    box-sizing: border-box;
    padding: 10px;
    margin: 2px;
  }
  
  body {
    background: #181818;
    color: #fefefe;
    font-family: "Geomanist", Helvetica, Arial, sans-serif;
    height: 500px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin: auto;
    width: 50%;
    padding: 10px;
    text-align: center;
  }
  
  h2 {
    text-align: left;
  }
  
  button,
  a.btn.btn-secondary {
    background-color: #181818;
    color: #fefefe;
    border-radius: 25%;
    box-shadow: #fefefe;
  }
  
  button:hover {
    box-shadow: 0 12px 16px 0 gray;
    cursor: pointer;
  }